.button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
	font-family: "ProximaNova", sans-serif;
	font-weight: 400;
	color: var(--c-white);
	font-size: 1.2rem;
	background-color: var(--c-primary);
	border-radius: 5px;
	width: 320px;
	height: 70px;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	position: relative;
	z-index: 1;
	border-radius: 50px;
	transition: box-shadow .2s ease-in;


	&:hover {
		box-shadow: var(--c-secondary) 0 -70px 0 inset;

		&::after {
			transform: translateX(5px);
		}
	}

	&::after {
		content: "";
		display: block;
		width: 16px;
		height: 12px;
		background: url(../../img/btn-arrow.svg);
		background-size: cover;
		margin-left: 1.5rem;
		transition: transform .2s linear;
	}

}